import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import Navbar from '../components/Navbar'
import { apiUrl, mediaUrl } from '../config'
import axios from 'axios'

export default function Users() {

    const [users, setUsers] = useState([])
    useEffect(() => {
        async function fetchData() {
            let result = await axios.get(`${apiUrl}user/users`)
            console.log(result)
            setUsers(result.data.message)
        }

        fetchData()
    }, [])

    
    async function changeStatus(id,status){
        let updCheck=await axios.put(`${apiUrl}user/status/${id}`,{status:status})
        console.log(updCheck)
    }

    return (
        <>
            <Navbar />
            <h1 className="text-center m-5">Users</h1>
            <div className="text-center m-5">
                <Link to="/add-user" className="btn btn-info">Add User</Link>
            </div>
            <div className="container">
                <table className="table">
                    <thead>
                        <tr>
                            <th >#</th>
                            <th >Name</th>
                            <th >Email</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            users.map((item, index) => {
                                return (
                                    <tr key={index}>
                                        <th >{index + 1}</th>
                                        <td>{item.email}</td>
                                        <td>{item.email}</td>
                                        <td>
                                            <button onClick={() => changeStatus(item._id,item.status)} className={item.status ? 'btn btn-danger' : 'btn btn-success'}>
                                                {item.status ? 'Disabled' : 'Activate'}
                                            </button>
                                        </td>
                                    </tr>
                                )
                            })
                        }


                    </tbody>
                </table>
            </div>  
        </>
    )
}
