import React,{useState} from 'react'
import Navbar from '../components/Navbar'
import axios from 'axios'
import { apiUrl } from '../config'
import { useNavigate } from 'react-router-dom'


export default function AddUser() {

    const navigate=useNavigate()

    const [name,setName] = useState("")
    const [password,setPassword] = useState("")
    const [email,setEmail] = useState("")

    const addedBy=localStorage.getItem("token")


    
    function addUser(event) {
        event.preventDefault()
        
        axios.post(`${apiUrl}user/add-user`,{name,email,password,addedBy}).then(res=>{
            console.log(res)
            if(res.status===200){
                navigate("/users")
            }else{
                alert('Please Try Again')
            }
        })
    }




    return (
        <>
            <Navbar />
            <h1 className="text-center">Add User</h1>
            <div className="container">
                <form onSubmit={addUser}>
                    <div className="form-group">
                        <label>User Name</label>
                        <input type="text" onChange={(e)=>setName(e.target.value)} value={name} className="form-control" id="exampleFormControlInput1" placeholder="Enter Name" />
                    </div>
                    <div className="form-group">
                        <label>User Email</label>
                        <input type="text" onChange={(e)=>setEmail(e.target.value)} value={email} className="form-control" id="exampleFormControlInput1" placeholder="Enter Name" />
                    </div>
                    <div className="form-group">
                        <label>User Password</label>
                        <input type="text" onChange={(e)=>setPassword(e.target.value)} value={password} className="form-control" id="exampleFormControlInput1" placeholder="Enter Name" />
                    </div>
                   
                    <button type="submit" className="btn btn-primary">Submit</button>
                </form>
            </div>
        </>
    )
}
