import React, { useState,useEffect } from 'react'
import Navbar from '../components/Navbar'
import axios from 'axios'
import { apiUrl } from '../config'
import { useNavigate } from 'react-router-dom'


export default function AddPro() {

    const navigate = useNavigate()

    const [pName, setPName] = useState("")
    const [category, setCategory] = useState("")
    const [pic, setPic] = useState("")
    const [description, setDescription] = useState("")
    const [price, setPrice] = useState("")
    const [getCat, setCat] = useState([])


    useEffect(() => {
        async function fetchData() {
            let result = await axios.get(`${apiUrl}category/categories`)
            setCat(result.data.msg)
        }
        fetchData()
    }, [])

    const addedBy = localStorage.getItem("token")


    function getFile(e) {
        setPic(e.target.files[0])
    }

    function addCategory(event) {
        event.preventDefault()
        const formData = new FormData()

        formData.append('name', pName)
        formData.append('pic', pic)
        formData.append('description', description)
        formData.append('addedBy', addedBy)
        formData.append('price', price)
        formData.append('category', category)

        axios.post(`${apiUrl}product/add-pro`, formData).then(res => {
            console.log(res)
            if (res.status === 200) {
                navigate("/products")
            } else {
                alert('Please Try Again')
            }
        })
    }




    return (
        <>
            <Navbar />
            <h1 className="text-center">Add Product</h1>
            <div className="container">
                <form onSubmit={addCategory}>
                    <div className="form-group">
                        <label>Product Name</label>
                        <input type="text" onChange={(e) => setPName(e.target.value)} value={pName} className="form-control" id="exampleFormControlInput1" placeholder="Enter Name" />
                    </div>
                    <div className="form-group">
                        <label>Product Price</label>
                        <input type="text" onChange={(e) => setPrice(e.target.value)} value={price} className="form-control" id="exampleFormControlInput1" placeholder="Enter Price" />
                    </div>
                    <div className="form-group">
                        <label>Product Category</label>
                        <select onChange={(e) => setCategory(e.target.value)} className="form-control" id="exampleFormControlSelect1">
                            <option disabled selected>Select Category</option>
                            {
                                getCat.map((cat, index) => {
                                    return (
                                        <option key={index} value={cat._id}>{cat.name.toUpperCase()}</option>
                                    )
                                })
                            }
                        </select>
                    </div>
                    <div className="form-group">
                        <label>Product Image</label>
                        <input type="file" class="form-control-file" onChange={getFile} id="exampleFormControlFile1" />
                    </div>
                    <div className="form-group">
                        <label>Product Description</label>
                        <textarea className="form-control" onChange={(e) => setDescription(e.target.value)} id="exampleFormControlTextarea1" rows="3">{setDescription}</textarea>
                    </div>
                    <button type="submit" className="btn btn-primary">Submit</button>
                </form>
            </div>
        </>
    )
}
