import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import Navbar from '../components/Navbar'
import { apiUrl,mediaUrl } from '../config'
import axios from 'axios'

export default function Product() {

    const [product, setProduct] = useState([])
    useEffect(() => {
        async function fetchData() {
            let result = await axios.get(`${apiUrl}product/products`)
            setProduct(result.data.msg)
        }

        fetchData()
    }, [])

    async function delPro(id){
        let res=await axios.delete(`${apiUrl}product/del/${id}`)
        if(res.data.status===200){
            alert('Product deleted successfully')
            window.location.reload()
        }
    }


    return (
        <>
            <Navbar />
            <h1 className="text-center m-5">Products</h1>
            <div className="text-center m-5">
                <Link to="/add-product" className="btn btn-info">Add Product</Link>
            </div>
            <div className="container">
                <table className="table">
                    <thead>
                        <tr>
                            <th >#</th>
                            <th >Name</th>
                            <th >Pic</th>
                            <th >Description</th>
                            <th >Price</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            product.map((item, index) => {
                                return (
                                    <tr key={index}>
                                        <th >{index+1}</th>
                                        <td>{item.name.toUpperCase()}</td>
                                        <td>
                                            <img height="50px" src={`${mediaUrl}products/${item.pic}`} />
                                        </td>
                                        <td>{item.description.substr(0,15)}...</td>
                                        <td>&#8377;{item.price}</td>
                                        <td>
                                            <button onClick={()=>delPro(item._id)} className="btn btn-danger">Delete</button>
                                        </td>
                                    </tr>
                                )
                            })
                        }


                    </tbody>
                </table>
            </div>
        </>
    )
}
