import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import Navbar from '../components/Navbar'
import { apiUrl, mediaUrl } from '../config'
import axios from 'axios'

// update-status/

export default function Orders() {

    const [orders, setOrders] = useState([])

    const status = ["pending", "processing", "completed", "cancelled"];

    async function orderStats(id,e){
        let status = e.target.value;
        let res= await axios.post(`${apiUrl}order/update-status/${id}`, {status})
        console.log(res)
    }



    useEffect(() => {
        async function getOrders() {
            const res = await axios.get(`${apiUrl}order/orders`)
            setOrders(res.data.msg)
        }

        getOrders()
    })


    return (
        <>
            <Navbar />
            <div className="container my-4">
                <h1 className="text-center mb-4">Orders</h1>
                {
                    orders.length == 0 ?
                        <h1 className="text-center">No Orders</h1> :
                        <table className="table table-dark">
                            <thead>
                                <tr>
                                    <th >#</th>
                                    <th >Name / Email</th>
                                    <th >Price</th>
                                    <th >Status</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    orders.map((order, index) => {
                                        return (
                                            <tr key={index}>
                                                <td>{index + 1}</td>
                                                <td>{order.name} / {order.email}</td>
                                                <td>{order.price}</td>
                                                <td>
                                                    <select className="form-control" onChange={(e)=>orderStats(order._id,e)}>
                                                        {
                                                            status.map((status, index) => {
                                                                return (
                                                                    <option key={index} selected={order.status == status ? true : false} disabled={order.status == status ? true : false} value={status}>{status}</option>
                                                                )
                                                            })
                                                        }
                                                    </select>
                                                </td>
                                            </tr>
                                        )
                                    })
                                }

                            </tbody>
                        </table>
                }
            </div>
        </>
    )
}
