import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import Navbar from '../components/Navbar'
import { apiUrl,mediaUrl } from '../config'
import axios from 'axios'

export default function Category() {

    const [category, setCategory] = useState([])
    useEffect(() => {
        async function fetchData() {
            let result = await axios.get(`${apiUrl}category/categories`)
            setCategory(result.data.msg)
        }

        fetchData()
    }, [])

    async function delCat(id){
        let res=await axios.delete(`${apiUrl}category/del/${id}`)
        if(res.data.status===200){
            alert('Category deleted successfully')
            window.location.reload()
        }
    }


    return (
        <>
            <Navbar />
            <h1 className="text-center m-5">Category</h1>
            <div className="text-center m-5">
                <Link to="/add-cat" className="btn btn-info">Add Category</Link>
            </div>
            <div className="container">
                <table className="table">
                    <thead>
                        <tr>
                            <th >#</th>
                            <th >Name</th>
                            <th >Pic</th>
                            <th >Description</th>
                            <th>Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            category.map((item, index) => {
                                return (
                                    <tr key={index}>
                                        <th >{index+1}</th>
                                        <td>{item.name.toUpperCase()}</td>
                                        <td>
                                            <img height="50px" src={`${mediaUrl}category/${item.pic}`} />
                                        </td>
                                        <td>{item.description.substr(0,15)}...</td>
                                        <td>
                                            <button onClick={()=>delCat(item._id)} className="btn btn-danger">Delete</button>
                                        </td>
                                    </tr>
                                )
                            })
                        }


                    </tbody>
                </table>
            </div>
        </>
    )
}
