import React, { useState } from 'react'
import '../App.css'
import axios from 'axios'
import { apiUrl } from '../config'
import { useNavigate } from 'react-router-dom'

export default function Login() {

    const navigate = useNavigate()
    const isLogined = localStorage.getItem('token')
    {
        isLogined != null ? navigate('/dashboard') : navigate('/')
    }

    const [email, setEmail] = useState()
    const [password, setPassword] = useState("")

    async function loginUser(e) {
        e.preventDefault()
        axios.post(`${apiUrl}user/login`, { email, password }).then(res => {
            if (res.status === 200) {
                localStorage.setItem("token", res.data.message.email)
            }
        }).catch(err => {
            console.log(err)
        })

    }


    return (
        <>
            <div className="jumbotron jumbotron-fluid login-jumbo text-center">
                <h1>Welcome To Admin Panel</h1>
                <h2>Login Here</h2>
            </div>
            <div className="container">
                <div className="row">
                    <div className="col-sm-4"></div>
                    <div className="col-sm-4">
                        <form onSubmit={loginUser}>
                            <div className="form-group">
                                <label>Email address</label>
                                <input onChange={(e) => setEmail(e.target.value)} type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" value={email} />
                            </div>
                            <div className="form-group">
                                <label>Password</label>
                                <input type="password" onChange={(e) => setPassword(e.target.value)} className="form-control" id="exampleInputPassword1" value={password} />
                            </div>
                            <button type="submit" className="btn btn-primary">Login</button>
                        </form>
                    </div>
                    <div className="col-sm-4"></div>
                </div>
            </div>
        </>
    )
}
