import React,{useState} from 'react'
import Navbar from '../components/Navbar'
import axios from 'axios'
import { apiUrl } from '../config'
import { useNavigate } from 'react-router-dom'


export default function AddCat() {

    const navigate=useNavigate()

    const [catName,setCatName] = useState("")
    const [pic,setPic] = useState("")
    const [description,setDescription] = useState("")

    const addedBy=localStorage.getItem("token")


    function getFile(e){
        setPic(e.target.files[0])
    }
    
    function addCategory(event) {
        event.preventDefault()
        const formData = new FormData()

        formData.append('name',catName)
        formData.append('pic',pic)
        formData.append('description',description)
        formData.append('addedBy',addedBy)

        axios.post(`${apiUrl}category/add-cat`,formData).then(res=>{
            console.log(res)
            if(res.status===200){
                navigate("/category")
            }else{
                alert('Please Try Again')
            }
        })
    }




    return (
        <>
            <Navbar />
            <h1 className="text-center">Add Category</h1>
            <div className="container">
                <form onSubmit={addCategory}>
                    <div className="form-group">
                        <label>Category Name</label>
                        <input type="text" onChange={(e)=>setCatName(e.target.value)} value={catName} className="form-control" id="exampleFormControlInput1" placeholder="Enter Name" />
                    </div>
                    <div className="form-group">
                        <label>Category Image</label>
                        <input type="file" class="form-control-file" onChange={getFile} id="exampleFormControlFile1" />
                    </div>
                    <div className="form-group">
                        <label>Category Description</label>
                        <textarea className="form-control" onChange={(e)=>setDescription(e.target.value)} id="exampleFormControlTextarea1" rows="3">{setDescription}</textarea>
                    </div>
                    <button type="submit" className="btn btn-primary">Submit</button>
                </form>
            </div>
        </>
    )
}
